.modal {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  margin: 0;
  padding: 30px;
  text-align: center;
  z-index: 9999;
}
.modal h2 {
  font-family: 'Roboto', sans-serif;
  font-size: 26px;
  margin: 0 0 30px;
}
.modal a {
  color: #4c4c4c;
  font-family: 'Encode Sans Condensed', sans-serif;
  text-decoration: none;
  transition: color 0.3s linear 0s;
}
.modal a:hover {
  color: #727272;
  text-decoration: underline;
}